.main-nav{
    min-height: 50px;
    width: 100%;
    margin: 0 auto;
    border-top: 3px solid #ef8236;
    box-shadow: 0 1px 5px rgb(0 0 0 / 20%);
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    background-color: #f8f9f9;
    display: flex !important;
    justify-content: center !important;
}

.navbar{
    height: 100%;
    min-width: 85%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-stackOverflow{
    padding: 5px 25px ;
}

.nav-item{
    margin: 0px 3px;
    font-size: small;
    font-weight: 500;
    text-decoration: none;
    color: rgb(69, 69, 69);
    transition: 0.2s;
}

.nav-btn{
    cursor: pointer;
    border-radius: 20px;
    padding: 10px 20px;
}

.stack-logo{
    width: 150px;
    height: 30px;
}

.nav-item:hover{
    background-color: rgb(226, 226, 226);
}

.navbar form input{
    min-width: 90%;
    margin: 0;
    padding: 8px 10px 8px 32px;
    font-size: 13px;
    border: solid 1px #0000003e;
    border-radius: 3px;
}
.navbar form{
    flex-grow: 1;
    padding: 0px 12px;
    position: relative;
}
.navbar form .search-icon{
    position: absolute;
    left:20px;
    top: 8px;
}

.nav-links{
    padding: 7px 13px;
    border: solid 1px blue;
    border-radius: 3px;
    background-color: #e7f8fe;
    cursor: pointer;
}

.nav-links:hover{
    background-color: #d3e4eb;
}